<template>
    <div class="content-box">
        <div class="title-box">
            <p class="header-line">
                <i class="fa fa-arrow-left m-r-10 pointer c-primary" aria-hidden="true" @click="back()"></i>
                {{$t('workorder.workorderDeploy')}}
            </p>
        </div>
        <div class="select-box">
            <mode-switch @mode-status="changeMode" ></mode-switch>
            <span class="vertical"></span>
            <select-group 
            @select-getDeviceOptions="getGroupOption" 
            @select-getGroupName="getGroupName" 
            :isSingleMode="isSingleMode"
            :groupType="groupTypeArray">
            </select-group>
            <select-device 
            @select-device="getDeviceOption"
            @isOfflineDev="isOfflineDev" 
            :isSingleMode="isSingleMode"
            :isDisabledOffDev="isDisabledOffDev"
            :groupType="groupType">
            </select-device>

            <div class="wdbox m-l-10">
                <span>{{$t('workorder.workorderName')}}</span>
                <span class="wdname">{{wdname}}</span>
            </div>
        </div>
        <div class="table-box" >
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane name="summary" v-loading="summaryLoading">
                    <span slot="label">
                        <i class="fa fa-sitemap m-r-5" aria-hidden="true"></i>
                        {{$t('workorder.summary')}}
