                                    <i class="fa fa-trash-o" aria-hidden="true" @click="deleteFile(props.item.wiid)"></i>
                                </div>
                                <span slot="card-body-img" slot-scope="props" >

                                    <img src="@/assets/imgs/deb.png" alt="no img" v-if="props.item.filename.indexOf('deb')>-1"/>
