                    </div>
                    <div class="ov-box" v-show="exeIsShow">
                        <p class="ov-header">{{$t('workorder.exePkg')}}</p>
                        <div class="ov-content">
                            <card-temp
                            :data="wdItems"  
                            :isAllShow = "false"
                            typeKey = "target"
                            :typeArray="[this.workorder.installExe]"
                            >
                                <div class="card-header cf" slot="card-header" slot-scope="props">
                                    <svg t="1592448887407" class="icon m-r-9 wd-icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3463" width="20" height="20">
                                        <path d="M465.92 488.96V222.208L189.44 281.6v207.36h276.48z m46.08 0h322.56V143.36l-322.56 69.12v276.48z m-46.08 46.08H189.44V742.4l276.48 59.392v-266.752z m46.08 0v276.48l322.56 69.12v-345.6h-322.56z" fill="#378f8c" p-id="3464"></path>
                                    </svg>
                                    
                                    <i class="fa fa-trash-o" aria-hidden="true" @click="deleteFile(props.item.wiid)"></i>
                                </div>
                                <img :src="getExeSrc(props.item.reid)" :onerror="defaultExeImg" alt="no img" slot="card-body-img" slot-scope="props"/>
                            </card-temp>
                        </div>
                    </div>
                    <div class="ov-box" v-show="zipIsShow">
                        <p class="ov-header">{{$t('workorder.compressedPkg')}}</p>
                        <div class="ov-content">
                            <card-temp
                            :data="wdItems"  
                            :isAllShow = "false"
                            typeKey = "target"
                            :typeArray="[this.workorder.installZip]"
                            >
                                <div class="card-header cf" slot="card-header" slot-scope="props">
                                    
                                    <svg t="1592448887407" class="icon m-r-9 wd-icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3463" width="20" height="20">
                                        <path d="M465.92 488.96V222.208L189.44 281.6v207.36h276.48z m46.08 0h322.56V143.36l-322.56 69.12v276.48z m-46.08 46.08H189.44V742.4l276.48 59.392v-266.752z m46.08 0v276.48l322.56 69.12v-345.6h-322.56z" fill="#378f8c" p-id="3464"></path>
                                    </svg>
                                    
                                    <i class="fa fa-trash-o" aria-hidden="true" @click="deleteFile(props.item.wiid)"></i>
                                </div>
                                <img src="@/assets/imgs/zip.png" alt="no img" slot="card-body-img" slot-scope="props"/>
                            </card-temp>
                        </div>
                    </div>
                    <!-- <div class="schedule-box" v-show="btIsShow">
                        <div class="schedule-header">
                            Schedule
                            <div class="schedule-switch">
                                <el-switch
                                v-model="isSchedule"
                                active-color="#5cb85c"
                                inactive-color="#dddddd">
                                </el-switch>
                            </div>
                        </div>
                        <div class="schedule-content">
                            <el-date-picker
                            v-model="scheduleTime"
                            type="datetime"
                            placeholder="Select Schedule Time"
                            size="small"
                            @change="test"
                            :disabled="!isSchedule">
                            </el-date-picker>
                        </div>
                    </div> -->
                    <div class="text-center noData" v-show="noDataIsShow">
                        <p>
                            <img src="@/assets/imgs/noData.png" alt="" style="">
                            {{$t('workorder.noData')}}
                        </p>
                        <el-button 
                        size="small" 
                        type="danger" 
                        @click="modify()"  
                        >
                            {{$t('workorder.addWorkorderItem')}}
                        </el-button>
                    </div>
                    <div class="deployBtn">
                        <el-button 
                        v-show="applyBtnIsShow"
                        size="small" 
                        type="default" 
                        @click="modify()" 
                        >
                            {{$t('global.modify')}}
                        </el-button>
                        <el-button 
                        @click="showDpwdDialog()"
                        v-show="applyBtnIsShow"
                        size="small" 
                        type="primary" 
                        >
                            {{$t('global.deploy')}}
                        </el-button>
                    </div>
                   
                </el-tab-pane>
                <el-tab-pane name="wdStatusHistory">
                    <span slot="label">
                        <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                        {{$t('workorder.dpwdHistory')}}
                    </span>
                    <el-table
                        :data="wdHistoryTableList"
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="selectItem"
                        @filter-change="filterStatus"
                        :default-sort = "{prop: 'ts', order: 'descending'}"
                        ref="wdHistoryTable"
                        v-loading="listLoading"
                        >
                        <el-table-column
                        type="selection"
                        min-width="50"
                        :selectable="checkSelectable">
                        </el-table-column>

                        <el-table-column
                        prop="devname"
                        :label="$t('workorder.deviceName')"
                        min-width="120"
                        >
                        </el-table-column>

                        <el-table-column
                        prop="taskname"
                        :label="$t('workorder.deployName')"
                        min-width="120"
                        >
                        </el-table-column>

                        <el-table-column
                        prop="filename"
                        :label="$t('workorder.fileName')"
                        min-width="140"
                        >
                        </el-table-column>

                        <el-table-column
                        prop="target"
                        :label="$t('workorder.funName')"
                        min-width="150"
                        
                            >
                            <template slot-scope="scope">
                                {{scope.row.target|wdStatusFunction}}
                            </template>
                        </el-table-column>

                        <el-table-column
                        :label="$t('workorder.beginTime')"
                        min-width="130"
                        prop="ts"
                            >
                            <template slot-scope="scope">
                                {{scope.row.ts|time}}
                            </template>
                        </el-table-column>

                        <el-table-column
                        :label="$t('workorder.finishTime')"
                        min-width="130"
                        prop="tsEnd"
                            >
                            <template slot-scope="scope">
                                {{scope.row.tsEnd|time}}
                            </template>
                        </el-table-column>

                        <el-table-column
                        column-key="status"
                        prop="status"
                        :label="$t('workorder.dpStatus')"
                        min-width="140"
                        :filters="[{ text: $t('global.success'), value: 2 }, { text: $t('global.failed'), value: 1 }, {text: $t('global.deploying'), value: 0}, {text: $t('global.waiting'), value: 3}]"
                        filter-placement="bottom-end"
                        >
                            <template slot-scope="scope">
                                <img src="@/assets/imgs/loading1.gif" alt="" style="width:50px;height:10px;"v-if="scope.row.status==0">
                                <span v-if="scope.row.status==2" class="badge bg-green">{{$t('global.success')}}</span> 
                                <span v-if="scope.row.status==1" class="badge bg-DimGray">{{$t('global.failed')}}</span>
                                <span v-if="scope.row.status==3" class="badge bg-yellow">{{$t('global.waiting')}}</span>
                                <!-- {{scope.row.upstatus|AppStatus}} -->
                            </template>
                        </el-table-column>

                        <el-table-column
                        :label="$t('workorder.errorStatus')"
                        min-width="120"
                        >
                            <template slot-scope="scope">
                                {{""+scope.row.errorCode|appErrorStatus}}
                            </template>
                        </el-table-column>

                        <el-table-column
                        :label="$t('global.action')"
                        min-width="175"
                        align="center">
                            <template slot-scope="scope">
                                <div>
                                    <el-button-group>
                                        <el-button 
                                            size="mini" 
                                            type="danger"
                                            @click="deleteHistory(scope.row.wsid)"
                                            :disabled="scope.row.status==0"
                                            >
                                                {{$t('global.delete')}}
                                        </el-button>
                                        <el-button 
                                            size="mini" 
                                            type="primary"
                                            @click="reExecWdDeploy(scope.row.wsid)"
                                            :disabled="scope.row.status==0|| scope.row.status==2"
                                            >
                                                {{$t('workorder.redeploy')}}
                                        </el-button>
                                    </el-button-group>
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>

                    <btn-group 
                    :selectedData="multipleSelection" 
                    :isDelete="true" 
                    :isMove="false" 
                    tid="wsid" 
                    :delApi="batchDeletewpStatusHistory" 
                    @result="isSuccess" 
                    v-show="isShowBtnGroup"
                    class="fl m-t-10 cf">
                    </btn-group>
                    <el-button 
                    v-loading="clearLoading" 
                    size="small" 
                    @click="clearAll()" 
                    type="primary" 
                    class="fl m-t-10 cf m-l-10"
                        v-show="isShowBtnGroup">
                    {{$t('global.clearAll')}}
                    </el-button>

                    <div class="m-t-10 cf fr">
                        <el-pagination
                        @current-change="handleCurrentChange"
                        layout="prev, pager, next"
                        :page-size="limit"
                        :current-page="currentPage"
                        :total="dataCount"
                        v-show="isshow"
                        background
                        >
                        </el-pagination>
                        
                    </div> 
                </el-tab-pane>
            </el-tabs>
        </div>
        <el-dialog :visible.sync="dialogDeployNameVisible" :close-on-click-modal="false">
            <div slot="title" class="dialog-title">
                <i class="fa fa-level-up m-r-10" ></i>
                {{$t('workorder.workorderDeploy')}}
            </div>
            <el-form :model="dpwdForm" :rules="rules" ref="dpwdForm">
                <el-form-item :label="$t('label.deployName')" label-width="230px" prop="deployName">
                    <el-input v-model="dpwdForm.deployName" autocomplete="off" class="max-w-350" :placeholder="$t('workorder.wdDeployNameHolder')"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.supportOfflineDeploy')" label-width="230px" v-show="!isSingleMode" >
                    <el-switch v-model="isSupportOffline"></el-switch>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogDeployNameVisible = false">{{$t("global.cancel")}}</el-button>
                <el-button type="primary" @click="wdDeploy()">{{$t("global.confirm")}}</el-button>
            </div>
        </el-dialog>
    </div>
   
</template>

<script>
    import {workorder} from '@/assets/js/lwm2mMap';
    import handleResponse from '@/components/restfulapi/handleResponse';

    import drawer from "./drawer"; 

    import selectDevice from '../../common/select-device'
    import modeSwitch from "../../common/mode-switch"
    import selectGroup from "../../common/select-group"

    import cardTemp from "../../common/cardTemp"
    import {getRepoSrcApi} from "../restfulapi/serverConfigApi"
    import {getRepoAppsApi} from '@/components/restfulapi/getRepoAppApi';
    import {groupType} from "../../assets/js/constant"
    
    import {
        getWorkorderApi,
        getWorkorderByNameApi,
        getWorkorderItemByNameApi,
        getWorkorderStatusByNameApi,
        editWorkorderApi,
        deleteWorkorderApi,
        deleteWorkorderStatusApi,
        batchDeleteWorkorderStatusApi,
        addWorkorderApi,
        addWorkorderItemApi,
        deleteWorkorderItemApi,
        execWorkorderApi,
        reExecWorkorderApi,
        batchExecWorkorderApi,
        deleteWorkorderStatusAllApi,
        getWorkorderItemByNameAndPageApi,
        getWorkorderItemByNameAndStatusAndPageApi,
        deleteWorkorderStatusByWdnameApi,
        getWorkorderStatusByPageInDevidAndWdApi,
        getWorkorderStatusByStatusAndPageInDevidAndWdApi,
        getWorkorderStatusByPageInGroupAndWdApi,
        getWorkorderStatusByStatusAndPageInGroupAndWdApi,
        deleteWorkorderStatusByDevIdApi,
        deleteWorkorderStatusByGroupApi
    } from '../restfulapi/workorderApi' 
    import {checkSession, getSession, setSession } from '../../assets/js/storage';
    import btnGroup from "../../common/btn-group"


    export default{
        name: 'workerorder-add',
        data(){
            let validateDepName = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "summary",
                wdname: "",
                appOptions: [],
                defaultImg: 'this.src="' + 'static/imgs/default_apk.png' + '"',
                defaultExeImg: 'this.src="' + 'static/imgs/default_windows.png' + '"',
                wdItems: [],
                workorder,
                appIsShow: false,
                kioskIsShow: false,
                btIsShow: false,
                composeIsShow: false,
                linuxPkgIsShow: false,
                exeIsShow: false,
                zipIsShow: false,

                //deploy wd
                selectedAgentId: '',
                selectedAgentsData: [],
                isSingleMode: true,
                groupname: '',
                dpwdForm: {
                    deployName: '',
                },
                rules:{
                    deployName: {required: true, validator: validateDepName, trigger: 'blur'},
                },
                //wd history
                multipleSelection: [],
                wdHistoryTableList: [],
                batchDeletewpStatusHistory: batchDeleteWorkorderStatusApi,
                limit: 10,
                dataCount: 0,
                currentPage: 1,
                isshow: false,
                dialogDeployNameVisible: false,
                isShowBtnGroup: false,
                intervalTime: 5000,
                clearLoading: false,
                //filter status
                statusArray: [],
                isDisabledOffDev: true,
                isOffline: false,

                summaryLoading: false,
                listLoading: false,
                isInterval: false,
                isSupportOffline: false,
		isSchedule: false,
                scheduleTime: "",
                wdtype: ""
            }
        },

        components: { 
            drawer,
            selectDevice,
            selectGroup,
            modeSwitch,
            btnGroup,
            cardTemp
        },

        methods:{
            test(){
                console.log(new Date(this.scheduleTime).getTime())
            },
            //batch selected
            selectItem(val) {
                this.multipleSelection = val;
            },

            modeChange(val){
                this.isSingleMode = val;
            },
            //get repo apps
            
            getSrc(raid){
                if(raid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getAppIcon/"+raid;
                }else{
                   return 'static/imgs/default_apk.png';
                }
                
            },
            getExeSrc(reid){
                if(reid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getExeIcon/"+reid;
                }else{
                   return 'static/imgs/default_windows.png';
                }
                
            },
            handleClick(value){  
                if(value.name === "wdStatusHistory"){
                    this.isDisabledOffDev = false;
                }else{
                     this.isDisabledOffDev = true;
                }
            },

            getWdItems(wdname){
                this.summaryLoading = true;
                getWorkorderItemByNameApi(wdname).then((data) => {
                    this.summaryLoading = false;
                    handleResponse(data, (res) => {
                        if(Object.prototype.toString.call(res) === "[object Array]"){
                            this.appIsShow = false;
                            this.kioskIsShow = false;
                            this.btIsShow = false;
                            this.composeIsShow = false;
                            this.linuxPkgIsShow = false;
                            this.zipIsShow = false;
                            this.exeIsShow = false;

                            for(let i in workorder){
                                let isHave = res.some((value) => {
                                    return value.target === workorder[i];
                                })
                                if(isHave){
                                    if(i === "preInstallApp"){
                                        this.appIsShow = true;
                                    }
                                    if(i === "kiosk"){    
                                        this.kioskIsShow = true;
                                    } 
                                    if(i === "bootanimation"){
                                        this.btIsShow = true;
                                    }
                                    if(i === "installDockerCompose"|| i==="installDockerSwarm"){
                                        this.composeIsShow = true;
                                    }
                                    if(i === "installLinuxPkg"){
                                        this.linuxPkgIsShow = true;
                                    }
                                    if(i === "installExe"){
                                        this.exeIsShow = true;
                                    }
                                    if(i === "installZip"){
                                        this.zipIsShow = true;
                                    }
                                } 
                            }  
                            this.wdItems =  res;
                            
                        }else{
                            console.error("data is not array")
                            return;
                        }
                    })
                })
            },

            deleteFile(wiid) {
                
                _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        this.summaryLoading = true;
                        deleteWorkorderItemApi(wiid).then((res) => {
                            this.summaryLoading = false;
                            handleResponse(res, (data) => {
                                if(data.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getWdItems(this.wdname);
                                    })
                                }else{
                                    _g.handleError(data);
                                }
                            })
                        })
                    }
                })
            },
            modify(){
                this.$router.push({path: "/main/workorder/details", query: { wdname: this.wdname, type: this.wdtype}});
            },

            changeMode(val){
                this.isSingleMode = val
            },

            getGroupName(groupname){
                this.groupname = groupname;
            },

            getDeviceOption(selectedAgentId){ 
                this.selectedAgentId = selectedAgentId;
            },

            getGroupOption(selectedAgentsData){
                this.selectedAgentsData = selectedAgentsData;
            },

            //exec workorder 
            wdDeploy(){
                this.$refs.dpwdForm.validate((valid) => {
                    if(valid) {
                        // single mode is true and batch mode is false

                        if(this.isSingleMode){
                            if(this.selectedDeviceId == ''){
                                this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                                return;
                            }
                            
                            this.summaryLoading = true;
                            this.dialogDeployNameVisible = false;
                            let stime;
                        if(this.scheduleTime){
                            stime = new Date(this.scheduleTime).getTime();
                        }
                            execWorkorderApi(this.dpwdForm.deployName, this.wdname, this.selectedAgentId, stime).then((data) => {
                                    this.summaryLoading = false;
                                handleResponse(data, (res) => {
                                    if(res.status === "CHANGED"){
                                        this.activeName = "wdStatusHistory";
                                        this.intervalGetDpwdStatus();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }else{
                            if(this.groupname == ""){
                                this.$swal("", this.$t('global.selectGroup'),"info", {button: this.$t('global.confirm')});
                                return;
                            }
                            if(this.selectedAgentsData.length === 0&& !this.isSupportOffline){
                                this.$swal("", this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')});
                                return;
                            }
                            this.summaryLoading = true;
                            this.dialogDeployNameVisible = false;
                            let stime;
                            if(this.scheduleTime){
                                stime = new Date(this.scheduleTime).getTime();
                            }
                            batchExecWorkorderApi(this.dpwdForm.deployName, this.wdname, this.groupname, this.isSupportOffline, stime).then((data) => {
                                    this.summaryLoading = false;
                                handleResponse(data, (res) => {
                                    if(res.status === "CHANGED"){
                                        this.dialogDeployNameVisible = false;
                                        this.activeName = "wdStatusHistory";
                                        this.intervalGetDpwdStatus();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }
                    }
                })
            },

            reExecWdDeploy(wsid){
                _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('global.repeat'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        reExecWorkorderApi(wsid).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.currentPage = 1;
                                    this.intervalGetDpwdStatus();
                                }else{
                                    _g.handleError(res)
                                }
                            })
                        })
                        
                    }
                }) 
            },

            showDpwdDialog(){
                if(this.isSingleMode){
                    if(this.selectedAgentId == ''){
                        this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                        return;
                    }
                }else{
                    if(this.groupname == ""){
                        this.$swal("", this.$t('global.selectGroup'),"info", {button: this.$t('global.confirm')});
                        return;
                    }
                    
                }
                //清空数据
                this.dpwdForm.deployName = "";
                this.dialogDeployNameVisible = true;
            },
            //wd status history
            intervalGetDpwdStatus(){
                if(this.isSingleMode){
                    this.getDpwdStatusByDevid();
                    if(_g.timer === null){
                        _g.timer = window.setInterval(() => {
                            this.getDpwdStatusByDevid();
                            this.isInterval = true;
                        },this.intervalTime)
                    }
                }else{
                    this.getDpwdStatusByGroup();
                    if(_g.timer === null){
                        _g.timer = window.setInterval(() => {
                            this.getDpwdStatusByGroup();
                            this.isInterval = true;
                        },this.intervalTime)
                    }
                }
               
            },

            getDpwdStatusByGroup(){
                if(this.isInterval === false){
                    this.listLoading = true;
                }
                if(this.statusArray.length>0){
                    getWorkorderStatusByStatusAndPageInGroupAndWdApi(this.wdname,this.groupname, this.currentPage, this.limit, this.statusArray).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            this.assignWdStatusData(res);
                        })
                    })
                }else{
                    getWorkorderStatusByPageInGroupAndWdApi(this.wdname, this.groupname, this.currentPage, this.limit).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            this.assignWdStatusData(res);
                        })
                    })
                }
                
            },

            getDpwdStatusByDevid(){
                if(this.isInterval === false){
                    this.listLoading = true;
                }
                if(this.statusArray.length>0){
                    getWorkorderStatusByStatusAndPageInDevidAndWdApi(this.wdname, this.selectedAgentId, this.currentPage, this.limit, this.statusArray).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            this.assignWdStatusData(res);
                        })
                    })
                }else{
                    getWorkorderStatusByPageInDevidAndWdApi(this.wdname, this.selectedAgentId, this.currentPage, this.limit).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            this.assignWdStatusData(res);
                        })
                    })
                }
            },


            assignWdStatusData(res){
                if(Object.prototype.toString.call(res) === "[object Object]"&& Object.prototype.toString.call(res.data) === '[object Array]'){
                    this.dataCount = res.count;
                    this.wdHistoryTableList = res.data;
                    this.isshow = this.dataCount > this.limit;
                    this.isShowBtnGroup = this.dataCount > 0;
                    let isFinish = true;
                    res.data.forEach((val) => {
                        if(val.status == 0){
                            isFinish = false;
                        }
                    })
                    if(isFinish){
                        window.clearInterval(_g.timer);
                        _g.timer = null;
                    }
                }else{
                    window.clearInterval(_g.timer);
                    _g.timer = null;
                }
                
            },

            filterStatus(value){
                this.statusArray = value.status;
                this.currentPage = 1;
                if(_g.isArray(this.statusArray)){
                    this.intervalGetDpwdStatus();
                }
               
            },

            selectItem(val) {
                this.multipleSelection = val;
            },

            handleCurrentChange(currentPage){
               if(currentPage){
                    this.currentPage = currentPage;
                }else{
                    this.currentPage = 1
                }
                this.intervalGetDpwdStatus();
            },
            
            isSuccess(result){
                if(result){
                    this.intervalGetDpwdStatus();
                }
            },
            checkSelectable(row) {
                return !row.status==0;
            },

            deleteHistory(wsid){
                _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        this.listLoading = true;
                        deleteWorkorderStatusApi(wsid).then((data) => {
                            this.listLoading = false;
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetDpwdStatus();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }
                 })
                
            },


            //clear All history
            clearAll(){
                _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.clearAll'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        this.clearLoading = true;
                        if(this.isSingleMode){
                            deleteWorkorderStatusByDevIdApi(this.wdname, this.selectedAgentId).then((data) => {
                                this.clearLoading = false;
                                handleResponse((data), (res) => {
                                    if(res.status === "CHANGED"){
                                        this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.intervalGetDpwdStatus();
                                        })
                                    }else{
                                        _g.handleError(res);
                                    }
                                })
                            })
                        }else{
                            deleteWorkorderStatusByGroupApi(this.wdname, this.groupname).then((data) => {
                                this.clearLoading = false;
                                handleResponse((data), (res) => {
                                    if(res.status === "CHANGED"){
                                        this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.intervalGetDpwdStatus();
                                        })
                                    }else{
                                        _g.handleError(res);
                                    }
                                })
                            })
                        }
                    }
                })
            },

            back(){
                this.$router.go(-1)
            },

            changeMode(val){
                this.initData();
                this.isSingleMode = !val
            },

            initData(){
                this.selectedAgentId = "";
                this.groupname = "";
                this.selectedAgentsData = [];
                this.scheduleTime="";
            },

            isOfflineDev(val){
                this.isOffline = val;
            }
        },
       

        computed: {
            noDataIsShow(){
                if(this.appIsShow || this.kioskIsShow 
                || this.btIsShow|| this.composeIsShow
                || this.linuxPkgIsShow|| this.exeIsShow|| this.zipIsShow){
                    return false;
                }else{
                    return true;
                }
            },
            applyBtnIsShow(){
                if(this.appIsShow || this.kioskIsShow 
                || this.btIsShow|| this.composeIsShow
                || this.linuxPkgIsShow|| this.exeIsShow|| this.zipIsShow){
                    return true;
                }else{
                    return false;
                }
            },
            
            groupType(){
                return this.wdtype;
            },

            groupTypeArray(){
                let groupArray = [];
                groupArray.push(this.wdtype);
                return groupArray;
            }
        },

        watch: {
            groupname(val){
                if(val){
                    this.wdHistoryTableList = [];
                    this.intervalGetDpwdStatus();
                    
                }  
    　　　　　},
            selectedAgentId(val){
                if(val){
                    this.wdHistoryTableList = [];
                    this.intervalGetDpwdStatus();
                    
                }  
    　　　　　},

        },

        created() {
            if(this.$route.query.wdname){
                this.wdname = this.$route.query.wdname;
                this.wdtype = this.$route.query.type;
            }else{
                console.error("wdname is error")
            }
            
            this.getWdItems(this.wdname);
        },

        beforeDestroy(){
            window.clearInterval(_g.timer);
            _g.timer = null;
        },

    }
</script>

<style lang="scss" scoped>
@import "../../assets/css/colors";

    $box-width: 170px;
    $box-height: 200px;
    .wd-intro{
        li{
            &:first-child{
                margin-top: 15px;
                i{
                    display: inline-block;
                    margin-left: 10px;
                    padding: 5px 10px;
                    border-bottom: 1px solid #000;
                }
            }

            &:nth-child(2){
                p{
                    &:first-child{
                        line-height: 26px;
                        font-size: 16px;
                        color: #000;
                        margin-bottom: 5px;
                    }
                    &:nth-child(2){
                        line-height: 26px;
                        font-size: 14px;
                        color: rgba(25,0,29,.5);
                        margin-bottom: 10px;
                    }
                }
               
                
            }
            padding: 10px;
            
        }
        margin-top: 20px;
        margin-right: 15px;
        padding: 10px;
        min-height: 418px;
        border: 1px solid #eee;
        border-top: 3px solid $warn-color;
        border-radius: 5px;
        -moz-box-shadow: 0 1px 12px 0 rgba(0,0,0,0.1);
        -webkit-box-shadow: 0 1px 12px 0 rgba(0,0,0,0.1);
        box-shadow: 0 1px 12px 0 rgba(0,0,0,0.1);
        &:hover{
            -moz-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            -webkit-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -ms-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
        }
    }
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }
    .deployBtn{
        margin:0 10px 25px 35px;
    }

    .ov-box{
        .ov-header{
            border-left: 4px solid #108cee;
            font-size: 21px;
            margin: 20px;
            padding-left: 15px;
            color: #141a24;
        }
        .ov-content{
            .card-header{
                i{
                    &:first-child{
                        float: left;
                        color: $darkgray-color
                    }
                    &:nth-child(2){
                        float: right;
                        cursor: pointer;
                        color: $primary-color;
                    }
                    
                    margin:5px 5px;
                }
                background: #ffffee
            }
            margin-left: 10px;
            margin-bottom: 20px;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
        }
        display: inline-block;
        margin-left: 20px;
    }
    .schedule-box{
        margin-left: 20px;
        .schedule-header{
            border-left: 4px solid #108cee;
            font-size: 21px;
            margin: 20px;
            padding-left: 15px;
            color: #141a24;

            .schedule-switch{
                display: inline-block;
                margin-left: 10px;
                position: relative;
                bottom: 2px;
            }
            
        }
        .schedule-content{
            .card-header{
                i{
                    &:first-child{
                        float: left;
                        color: $darkgray-color
                    }
                    &:nth-child(2){
                        float: right;
                        cursor: pointer;
                        color: $primary-color;
                    }
                    
                    margin:5px 5px;
                }
                background: #ffffee
            }
            margin-left: 20px;
            margin-bottom: 20px;
        }
        
    }

    .badge{
        display: inline-block;
        min-width: 10px;
        padding: 5px 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1;
        color: #fff;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        // background-color: #777;
        border-radius: 10px;
    }
    .wd-icon{
        display: inline-block;
        position: relative;
        top:3px;
    }

    .content-box{
        display: flex;
        flex-direction: column;
        .select-box{
            .wdbox{
                display: inline-block;
                .wdname{
                    display: inline-block;
                    min-width: 35px;
                    text-align: center;
                    border-bottom: 1px solid #ffffff;
                    margin-left: 5px;
                    font-style:italic;
                    padding: 5px;
                }
            }
        }
    } 
    
</style>



